export default{
    name:'search',
    data () {
        return{
            value:''
        }
    },
    mounted(){
        this.$refs.searchinput.blur();
        this.$nextTick(()=>{
            this.$refs.searchinput.focus();
        });
        
    },
    methods:{
        jump(){
            this.$refs.searchinput.blur();
            this.$router.go(-1);
        }
    }
}



